.Toolbar{
  height: 56px;
  width: 100%;
  position: fixed;
  top : 0;
  left: 0;
  background-color: #703B09;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box;
  z-index: 80;
}

.Toolbar nav {
  height: 100%;
}

@media (max-width:499px) {
  .DesktopOnly {
    display: none;
  }
}

@media (min-width:500px) {
    .MobileOnly {
      display: none;
    }

}
